import React, { useEffect, useRef, useState } from 'react';
import { useGoogleMaps } from '../contexts/GoogleMapsContext';
import { useAppSelector, useAppDispatch } from '../store';
import { addNotification } from '../store/slices/uiSlice';
import { getUserAssignedRegions, getRegionStyle, createRegionInfoContent } from '../utils/regionMapping';
import GISToolsManager from '../components/tools/GISToolsManager';

const MapPage: React.FC = () => {
  const mapContainerRef = useRef<HTMLDivElement>(null);
  const dataLayerRef = useRef<google.maps.Data | null>(null);
  const { isLoaded, loadError, createMap } = useGoogleMaps();
  const { mapInstance, isMapLoaded } = useAppSelector((state) => state.map);
  const { user } = useAppSelector((state) => state.auth);
  const dispatch = useAppDispatch();
  const [assignedRegions, setAssignedRegions] = useState<string[]>([]);
  const [showOnlyAssigned, setShowOnlyAssigned] = useState(false);
  const [showMapControls, setShowMapControls] = useState(true);
  const [showLegend, setShowLegend] = useState(true);
  const [currentCoords, setCurrentCoords] = useState({ lat: 20.5937, lng: 78.9629 });

  // Get user's assigned regions
  useEffect(() => {
    if (user) {
      const regions = getUserAssignedRegions(user);
      setAssignedRegions(regions);
    }
  }, [user]);

  // Create map instance
  useEffect(() => {
    if (isLoaded && mapContainerRef.current && !mapInstance) {
      try {
        const map = createMap(mapContainerRef.current);
        console.log('Map created successfully:', map);

        // Add mousemove listener for coordinates
        map.addListener('mousemove', (e: google.maps.MapMouseEvent) => {
          if (e.latLng) {
            setCurrentCoords({
              lat: e.latLng.lat(),
              lng: e.latLng.lng()
            });
          }
        });

        dispatch(addNotification({
          type: 'success',
          title: 'Map Loaded',
          message: 'Google Maps loaded - All tools ready in India',
          autoClose: true,
          duration: 3000,
        }));
      } catch (error) {
        console.error('Error creating map:', error);
        dispatch(addNotification({
          type: 'error',
          title: 'Map Load Error',
          message: 'Failed to load Google Maps',
          autoClose: false,
        }));
      }
    }
  }, [isLoaded, createMap, mapInstance, dispatch]);

  // Load and style India GeoJSON with user's assigned regions highlighted
  useEffect(() => {
    if (mapInstance && assignedRegions.length > 0) {
      // Create a data layer if it doesn't exist
      if (!dataLayerRef.current) {
        dataLayerRef.current = new google.maps.Data();
        dataLayerRef.current.setMap(mapInstance);
      }

      const dataLayer = dataLayerRef.current;

      // Load India GeoJSON from public folder
      dataLayer.loadGeoJson('/india.json', {}, (features) => {
        console.log('Loaded features:', features.length);

        dispatch(addNotification({
          type: 'info',
          title: 'Regions Loaded',
          message: `Loaded ${features.length} regions - ${assignedRegions.length} assigned to you`,
          autoClose: true,
          duration: 3000,
        }));
      });

      // Style features based on assigned regions
      dataLayer.setStyle((feature) => {
        const stateNameRaw = feature.getProperty('NAME_1') ||
                             feature.getProperty('ST_NM') ||
                             feature.getProperty('name');
        const stateName = String(stateNameRaw || '');

        const isHighlighted = assignedRegions.some(region =>
          stateName.toLowerCase().includes(region.toLowerCase()) ||
          region.toLowerCase().includes(stateName.toLowerCase()) ||
          stateName.toLowerCase() === region.toLowerCase()
        );

        const style = getRegionStyle(stateName, assignedRegions, isHighlighted);

        // Hide non-assigned regions if showOnlyAssigned is true
        if (showOnlyAssigned && !isHighlighted) {
          return {
            ...style,
            visible: false
          };
        }

        return style;
      });

      // Add click listener for region info
      dataLayer.addListener('click', (event: google.maps.Data.MouseEvent) => {
        const stateNameRaw = event.feature.getProperty('NAME_1') ||
                             event.feature.getProperty('ST_NM') ||
                             event.feature.getProperty('name');
        const stateName = String(stateNameRaw || 'Unknown Region');

        const isAssigned = assignedRegions.some(region =>
          stateName.toLowerCase().includes(region.toLowerCase()) ||
          region.toLowerCase().includes(stateName.toLowerCase()) ||
          stateName.toLowerCase() === region.toLowerCase()
        );

        const infoWindow = new google.maps.InfoWindow({
          content: createRegionInfoContent(stateName, isAssigned, user),
          position: event.latLng
        });

        infoWindow.open(mapInstance);
      });

      // Add mouseover effect
      dataLayer.addListener('mouseover', (event: google.maps.Data.MouseEvent) => {
        const stateNameRaw = event.feature.getProperty('NAME_1') ||
                             event.feature.getProperty('ST_NM') ||
                             event.feature.getProperty('name');
        const stateName = String(stateNameRaw || '');

        const isHighlighted = assignedRegions.some(region =>
          stateName.toLowerCase().includes(region.toLowerCase()) ||
          region.toLowerCase().includes(stateName.toLowerCase())
        );

        if (isHighlighted) {
          dataLayer.overrideStyle(event.feature, {
            fillOpacity: 0.7,
            strokeWeight: 3
          });
        }
      });

      dataLayer.addListener('mouseout', (event: google.maps.Data.MouseEvent) => {
        dataLayer.revertStyle(event.feature);
      });

      // Cleanup function
      return () => {
        if (dataLayerRef.current) {
          dataLayerRef.current.setMap(null);
          dataLayerRef.current = null;
        }
      };
    }
  }, [mapInstance, assignedRegions, showOnlyAssigned, user, dispatch]);

  const toggleShowOnlyAssigned = () => {
    setShowOnlyAssigned(!showOnlyAssigned);
  };

  if (loadError) {
    return (
      <div className="h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
        <div className="text-center">
          <div className="mx-auto h-12 w-12 flex items-center justify-center bg-red-100 rounded-lg mb-4">
            <svg className="h-6 w-6 text-red-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
            </svg>
          </div>
          <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
            Map Loading Error
          </h3>
          <p className="text-sm text-gray-500 dark:text-gray-400 mb-4">
            {loadError}
          </p>
          <button
            onClick={() => window.location.reload()}
            className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
          >
            Retry Loading
          </button>
        </div>
      </div>
    );
  }

  if (!isLoaded) {
    return (
      <div className="h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
            Loading Maps
          </h3>
          <p className="text-sm text-gray-500 dark:text-gray-400">
            Please wait while we load the Google Maps API...
          </p>
        </div>
      </div>
    );
  }

  return (
    <div className="h-screen flex flex-col overflow-hidden">
      {/* Map Header */}
      <div className="bg-white dark:bg-gray-800 shadow-sm border-b border-gray-200 dark:border-gray-700 px-4 py-3 flex-shrink-0">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-xl font-semibold text-gray-900 dark:text-white">
              Network Map
            </h1>
            <p className="text-sm text-gray-500 dark:text-gray-400">
              Telecom Infrastructure Visualization with GIS Tools
            </p>
          </div>
          <div className="flex items-center space-x-4">
            <div className="flex items-center space-x-2">
              <div className={`w-2 h-2 rounded-full ${isMapLoaded ? 'bg-green-500' : 'bg-yellow-500'}`}></div>
              <span className="text-sm text-gray-600 dark:text-gray-400">
                {isMapLoaded ? 'Map Loaded' : 'Loading...'}
              </span>
            </div>
          </div>
        </div>
      </div>

      {/* Map Container - Takes remaining height (100vh - header) */}
      <div className="flex-1 relative overflow-hidden">
        <div
          ref={mapContainerRef}
          className="w-full h-full"
        />

        {/* GIS Tools Manager - Top Right Corner - Sticky */}
        {isMapLoaded && mapInstance && (
          <div className="absolute top-4 right-4 z-30">
            <GISToolsManager map={mapInstance} />
          </div>
        )}

        {/* Bottom Left Controls - Side by Side */}
        <div className="absolute bottom-4 left-4 flex space-x-2 z-10">
          {/* Map Controls */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-xl border border-gray-200 dark:border-gray-700 overflow-hidden">
            <div
              className="flex items-center justify-between px-3 py-2 border-b border-gray-200 dark:border-gray-700 cursor-pointer bg-gray-50 dark:bg-gray-900"
              onClick={() => setShowMapControls(!showMapControls)}
            >
              <h3 className="text-xs font-semibold text-gray-900 dark:text-white flex items-center">
                <svg className="w-3 h-3 mr-1.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6V4m0 2a2 2 0 100 4m0-4a2 2 0 110 4m-6 8a2 2 0 100-4m0 4a2 2 0 100 4m0-4v2m0-6V4m6 6v10m6-2a2 2 0 100-4m0 4a2 2 0 100 4m0-4v2m0-6V4" />
                </svg>
                Controls
              </h3>
              <svg
                className={`w-3 h-3 transition-transform text-gray-500 ${showMapControls ? 'rotate-180' : ''}`}
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </div>

            {showMapControls && (
              <div className="p-2 space-y-1.5 w-48">
                <button
                  onClick={toggleShowOnlyAssigned}
                  className={`w-full text-left px-2 py-1.5 text-xs rounded-md transition-colors ${
                    showOnlyAssigned
                      ? 'bg-blue-100 dark:bg-blue-900 text-blue-800 dark:text-blue-200'
                      : 'text-gray-700 dark:text-gray-300 hover:bg-gray-100 dark:hover:bg-gray-700'
                  }`}
                >
                  {showOnlyAssigned ? ' ' : ''}Show Only Assigned
                </button>
              </div>
            )}
          </div>

          {/* Legend - Side by Side */}
          <div className="bg-white dark:bg-gray-800 rounded-lg shadow-xl border border-gray-200 dark:border-gray-700 overflow-hidden">
            <div
              className="flex items-center justify-between px-3 py-2 border-b border-gray-200 dark:border-gray-700 cursor-pointer bg-gray-50 dark:bg-gray-900"
              onClick={() => setShowLegend(!showLegend)}
            >
              <h3 className="text-xs font-semibold text-gray-900 dark:text-white flex items-center">
                <svg className="w-3 h-3 mr-1.5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
                Legend
              </h3>
              <svg
                className={`w-3 h-3 transition-transform text-gray-500 ${showLegend ? 'rotate-180' : ''}`}
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
              </svg>
            </div>

            {showLegend && (
              <div className="p-3 w-56">
                {/* Region Legend */}
                <div className="mb-2 pb-2 border-b border-gray-200 dark:border-gray-700">
                  <p className="text-xs font-medium text-gray-700 dark:text-gray-300 mb-1.5">Regions</p>
                  <div className="space-y-1">
                    <div className="flex items-center space-x-1.5">
                      <div className="w-3 h-2 bg-blue-500 opacity-50 border border-blue-700"></div>
                      <span className="text-xs text-gray-600 dark:text-gray-400">Assigned ({assignedRegions.length})</span>
                    </div>
                    <div className="flex items-center space-x-1.5">
                      <div className="w-3 h-2 bg-gray-300 opacity-20 border border-gray-400"></div>
                      <span className="text-xs text-gray-600 dark:text-gray-400">Other Regions</span>
                    </div>
                  </div>
                </div>

                {/* Infrastructure Legend */}
                <div className="mb-2 pb-2 border-b border-gray-200 dark:border-gray-700">
                  <p className="text-xs font-medium text-gray-700 dark:text-gray-300 mb-1.5">Infrastructure</p>
                  <div className="space-y-1">
                    <div className="flex items-center space-x-1.5">
                      <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                      <span className="text-xs text-gray-600 dark:text-gray-400">Active</span>
                    </div>
                    <div className="flex items-center space-x-1.5">
                      <div className="w-2 h-2 bg-yellow-500 rounded-full"></div>
                      <span className="text-xs text-gray-600 dark:text-gray-400">Maintenance</span>
                    </div>
                    <div className="flex items-center space-x-1.5">
                      <div className="w-2 h-2 bg-red-500 rounded-full"></div>
                      <span className="text-xs text-gray-600 dark:text-gray-400">Offline</span>
                    </div>
                  </div>
                </div>

                {/* User Info */}
                {user && (
                  <div>
                    <p className="text-xs text-gray-500 dark:text-gray-400">
                      <span className="font-medium">{user.name}</span>
                    </p>
                    <p className="text-xs text-blue-600 dark:text-blue-400 mt-0.5">
                      {assignedRegions.length} region{assignedRegions.length !== 1 ? 's' : ''}
                    </p>
                  </div>
                )}
              </div>
            )}
          </div>
        </div>

        {/* Coordinates Display - Bottom Right */}
        <div className="absolute bottom-4 right-4 bg-white dark:bg-gray-800 rounded-lg shadow-lg border border-gray-200 dark:border-gray-700 px-2.5 py-1.5 z-10">
          <div className="flex items-center space-x-1.5">
            <svg className="w-3 h-3 text-gray-500 dark:text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
            </svg>
            <span className="text-xs font-mono text-gray-600 dark:text-gray-400">
              {currentCoords.lat.toFixed(4)}, {currentCoords.lng.toFixed(4)}
            </span>
          </div>
        </div>

        {/* Loading Overlay */}
        {!isMapLoaded && isLoaded && (
          <div className="absolute inset-0 bg-gray-50 dark:bg-gray-900 bg-opacity-75 flex items-center justify-center z-50">
            <div className="text-center">
              <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mx-auto mb-2"></div>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Initializing map...
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default MapPage;