import React, { useState } from 'react';
import type { GISToolType } from '../../types/gisTools.types';
import DistanceMeasurementTool from './DistanceMeasurementTool';
import PolygonDrawingTool from './PolygonDrawingTool';
import CircleDrawingTool from './CircleDrawingTool';
import ElevationProfileTool from './ElevationProfileTool';
import InfrastructureManagementTool from './InfrastructureManagementTool';

interface GISToolsManagerProps {
  map: google.maps.Map | null;
}

interface Tool {
  id: GISToolType;
  name: string;
  description: string;
  icon: React.ReactNode;
  color: string;
}

/**
 * GIS Tools Manager - Central hub for all GIS tools
 * Provides a unified interface to access all Phase 4 tools
 */
const GISToolsManager: React.FC<GISToolsManagerProps> = ({ map }) => {
  const [activeTool, setActiveTool] = useState<GISToolType | null>(null);
  const [isExpanded, setIsExpanded] = useState<boolean>(true);

  const tools: Tool[] = [
    {
      id: 'distance',
      name: 'Distance Measurement',
      description: 'Measure distances between multiple points',
      color: 'blue',
      icon: (
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 20l-5.447-2.724A1 1 0 013 16.382V5.618a1 1 0 011.447-.894L9 7m0 13l6-3m-6 3V7m6 10l4.553 2.276A1 1 0 0021 18.382V7.618a1 1 0 00-.553-.894L15 4m0 13V4m0 0L9 7" />
        </svg>
      )
    },
    {
      id: 'polygon',
      name: 'Polygon Drawing',
      description: 'Draw polygons and calculate area',
      color: 'green',
      icon: (
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 21a4 4 0 01-4-4V5a2 2 0 012-2h4a2 2 0 012 2v12a4 4 0 01-4 4zm0 0h12a2 2 0 002-2v-4a2 2 0 00-2-2h-2.343M11 7.343l1.657-1.657a2 2 0 012.828 0l2.829 2.829a2 2 0 010 2.828l-8.486 8.485M7 17h.01" />
        </svg>
      )
    },
    {
      id: 'circle',
      name: 'Circle/Radius',
      description: 'Draw circles with custom radius',
      color: 'purple',
      icon: (
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 10a1 1 0 011-1h4a1 1 0 011 1v4a1 1 0 01-1 1h-4a1 1 0 01-1-1v-4z" />
        </svg>
      )
    },
    {
      id: 'elevation',
      name: 'Elevation Profile',
      description: 'View elevation data and terrain profile',
      color: 'orange',
      icon: (
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 12l3-3 3 3 4-4M8 21l4-4 4 4M3 4h18M4 4h16v12a1 1 0 01-1 1H5a1 1 0 01-1-1V4z" />
        </svg>
      )
    },
    {
      id: 'infrastructure',
      name: 'Infrastructure',
      description: 'Manage POPs and SubPOPs',
      color: 'red',
      icon: (
        <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
        </svg>
      )
    }
  ];

  const selectTool = (toolId: GISToolType) => {
    if (activeTool === toolId) {
      setActiveTool(null);
    } else {
      setActiveTool(toolId);
    }
  };

  const closeTool = () => {
    setActiveTool(null);
  };

  const getColorClasses = (color: string, isActive: boolean) => {
    const colors = {
      blue: isActive
        ? 'bg-blue-600 text-white'
        : 'bg-blue-50 dark:bg-blue-900/20 text-blue-700 dark:text-blue-400 hover:bg-blue-100 dark:hover:bg-blue-900/30',
      green: isActive
        ? 'bg-green-600 text-white'
        : 'bg-green-50 dark:bg-green-900/20 text-green-700 dark:text-green-400 hover:bg-green-100 dark:hover:bg-green-900/30',
      purple: isActive
        ? 'bg-purple-600 text-white'
        : 'bg-purple-50 dark:bg-purple-900/20 text-purple-700 dark:text-purple-400 hover:bg-purple-100 dark:hover:bg-purple-900/30',
      orange: isActive
        ? 'bg-orange-600 text-white'
        : 'bg-orange-50 dark:bg-orange-900/20 text-orange-700 dark:text-orange-400 hover:bg-orange-100 dark:hover:bg-orange-900/30',
      red: isActive
        ? 'bg-red-600 text-white'
        : 'bg-red-50 dark:bg-red-900/20 text-red-700 dark:text-red-400 hover:bg-red-100 dark:hover:bg-red-900/30',
    };
    return colors[color as keyof typeof colors] || colors.blue;
  };

  return (
    <>
      {/* Floating Tool Panel */}
      <div className="absolute top-4 right-4 bg-white dark:bg-gray-800 rounded-lg shadow-xl border border-gray-200 dark:border-gray-700 z-20 overflow-hidden">
        {/* Header */}
        <div className="flex items-center justify-between p-4 border-b border-gray-200 dark:border-gray-700">
          <h3 className="text-sm font-semibold text-gray-900 dark:text-white flex items-center">
            <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 6V4m0 2a2 2 0 100 4m0-4a2 2 0 110 4m-6 8a2 2 0 100-4m0 4a2 2 0 100 4m0-4v2m0-6V4m6 6v10m6-2a2 2 0 100-4m0 4a2 2 0 100 4m0-4v2m0-6V4" />
            </svg>
            GIS Tools
          </h3>
          <button
            onClick={() => setIsExpanded(!isExpanded)}
            className="text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
          >
            <svg
              className={`w-5 h-5 transition-transform ${isExpanded ? 'rotate-180' : ''}`}
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 9l-7 7-7-7" />
            </svg>
          </button>
        </div>

        {/* Tools Grid */}
        {isExpanded && (
          <div className="p-3 space-y-2 max-w-xs">
            {tools.map((tool) => (
              <button
                key={tool.id}
                onClick={() => selectTool(tool.id)}
                className={`w-full p-3 rounded-lg transition-all duration-200 text-left ${getColorClasses(
                  tool.color,
                  activeTool === tool.id
                )}`}
              >
                <div className="flex items-start space-x-3">
                  <div className="flex-shrink-0 mt-0.5">{tool.icon}</div>
                  <div className="flex-1 min-w-0">
                    <h4 className="text-sm font-medium mb-0.5">{tool.name}</h4>
                    <p className={`text-xs ${activeTool === tool.id ? 'text-white/80' : 'text-gray-500 dark:text-gray-400'}`}>
                      {tool.description}
                    </p>
                  </div>
                  {activeTool === tool.id && (
                    <svg className="w-5 h-5 flex-shrink-0" fill="currentColor" viewBox="0 0 20 20">
                      <path
                        fillRule="evenodd"
                        d="M16.707 5.293a1 1 0 010 1.414l-8 8a1 1 0 01-1.414 0l-4-4a1 1 0 011.414-1.414L8 12.586l7.293-7.293a1 1 0 011.414 0z"
                        clipRule="evenodd"
                      />
                    </svg>
                  )}
                </div>
              </button>
            ))}
          </div>
        )}

        {/* Active Tool Indicator (when collapsed) */}
        {!isExpanded && activeTool && (
          <div className="px-4 pb-4">
            <div className="text-xs text-gray-500 dark:text-gray-400">
              Active: {tools.find((t) => t.id === activeTool)?.name}
            </div>
          </div>
        )}
      </div>

      {/* Render Active Tool */}
      {activeTool === 'distance' && <DistanceMeasurementTool map={map} onClose={closeTool} />}
      {activeTool === 'polygon' && <PolygonDrawingTool map={map} onClose={closeTool} />}
      {activeTool === 'circle' && <CircleDrawingTool map={map} onClose={closeTool} />}
      {activeTool === 'elevation' && <ElevationProfileTool map={map} onClose={closeTool} />}
      {activeTool === 'infrastructure' && <InfrastructureManagementTool map={map} onClose={closeTool} />}
    </>
  );
};

export default GISToolsManager;