import React, { useState, useEffect } from 'react';
import type { Infrastructure, InfrastructureType, InfrastructureFilters } from '../../types/gisTools.types';

interface InfrastructureManagementToolProps {
  map: google.maps.Map | null;
  onClose?: () => void;
}

/**
 * Infrastructure Management Tool - Phase 4.5
 * KML import and manual POP/SubPOP management
 */
const InfrastructureManagementTool: React.FC<InfrastructureManagementToolProps> = ({
  map,
  onClose
}) => {
  const [infrastructures, setInfrastructures] = useState<Infrastructure[]>([]);
  const [markers, setMarkers] = useState<Map<string, google.maps.Marker>>(new Map());
  const [selectedInfra, setSelectedInfra] = useState<Infrastructure | null>(null);
  const [showAddForm, setShowAddForm] = useState<boolean>(false);
  const [showTable, setShowTable] = useState<boolean>(false);
  const [filters, setFilters] = useState<InfrastructureFilters>({});
  const [isPlacingMarker, setIsPlacingMarker] = useState<boolean>(false);
  const [newInfraLocation, setNewInfraLocation] = useState<{ lat: number; lng: number } | null>(null);

  // Form state for adding/editing
  const [formData, setFormData] = useState<Partial<Infrastructure>>({
    type: 'POP',
    name: '',
    networkId: '',
    contactName: '',
    contactNo: '',
    isRented: false,
    structureType: 'Tower',
    upsAvailability: false,
    backupCapacity: '',
    powerSource: 'Grid',
    status: 'Active'
  });

  // Load infrastructures from localStorage on mount
  useEffect(() => {
    loadInfrastructures();
  }, []);

  // Click listener for placing markers
  useEffect(() => {
    if (!map || !isPlacingMarker) return;

    const clickListener = map.addListener('click', (e: google.maps.MapMouseEvent) => {
      if (e.latLng) {
        setNewInfraLocation({ lat: e.latLng.lat(), lng: e.latLng.lng() });
        setIsPlacingMarker(false);
      }
    });

    return () => {
      google.maps.event.removeListener(clickListener);
    };
  }, [map, isPlacingMarker]);

  // Render markers when infrastructures change
  useEffect(() => {
    if (!map) return;
    renderMarkers();
  }, [infrastructures, map]);

  /**
   * Load infrastructures from localStorage
   */
  const loadInfrastructures = () => {
    const saved = localStorage.getItem('gis_infrastructures');
    if (saved) {
      const parsed = JSON.parse(saved);
      // Convert date strings back to Date objects
      const infrastructuresWithDates = parsed.map((infra: any) => ({
        ...infra,
        createdOn: new Date(infra.createdOn),
        updatedOn: new Date(infra.updatedOn),
        agreementDates: infra.agreementDates ? {
          start: new Date(infra.agreementDates.start),
          end: new Date(infra.agreementDates.end)
        } : undefined
      }));
      setInfrastructures(infrastructuresWithDates);
    }
  };

  /**
   * Save infrastructures to localStorage
   */
  const saveInfrastructures = (infras: Infrastructure[]) => {
    localStorage.setItem('gis_infrastructures', JSON.stringify(infras));
    setInfrastructures(infras);
  };

  /**
   * Render markers on map
   */
  const renderMarkers = () => {
    if (!map) return;

    // Clear existing markers
    markers.forEach(marker => marker.setMap(null));
    const newMarkers = new Map<string, google.maps.Marker>();

    // Filter infrastructures
    const filtered = applyFilters(infrastructures);

    // Create markers for each infrastructure
    filtered.forEach(infra => {
      const icon = getMarkerIcon(infra);

      const marker = new google.maps.Marker({
        position: infra.coordinates,
        map: map,
        title: infra.name,
        icon: icon,
        label: infra.source === 'KML' ? undefined : {
          text: infra.type === 'POP' ? 'P' : 'S',
          color: 'white',
          fontWeight: 'bold'
        }
      });

      // Add click listener
      marker.addListener('click', () => {
        setSelectedInfra(infra);
        showInfoWindow(marker, infra);
      });

      newMarkers.set(infra.id, marker);
    });

    setMarkers(newMarkers);
  };

  /**
   * Get marker icon based on infrastructure properties
   */
  const getMarkerIcon = (infra: Infrastructure): google.maps.Symbol => {
    // Different colors for KML vs Manual
    const isKML = infra.source === 'KML';
    const isPOP = infra.type === 'POP';

    let color = '';
    if (isKML) {
      color = isPOP ? '#4CAF50' : '#8BC34A'; // Green shades for KML
    } else {
      color = isPOP ? '#2196F3' : '#03A9F4'; // Blue shades for Manual
    }

    return {
      path: google.maps.SymbolPath.CIRCLE,
      fillColor: color,
      fillOpacity: 1,
      strokeColor: '#ffffff',
      strokeWeight: 2,
      scale: isPOP ? 10 : 7
    };
  };

  /**
   * Show info window for infrastructure
   */
  const showInfoWindow = (marker: google.maps.Marker, infra: Infrastructure) => {
    const content = `
      <div class="p-2">
        <h3 class="font-bold text-lg mb-2">${infra.name}</h3>
        <div class="text-sm space-y-1">
          <p><strong>Type:</strong> ${infra.type}</p>
          <p><strong>ID:</strong> ${infra.uniqueId || infra.networkId}</p>
          <p><strong>Status:</strong> <span class="px-2 py-1 rounded ${
            infra.status === 'Active' ? 'bg-green-100 text-green-800' :
            infra.status === 'Inactive' ? 'bg-gray-100 text-gray-800' :
            infra.status === 'Maintenance' ? 'bg-yellow-100 text-yellow-800' :
            'bg-blue-100 text-blue-800'
          }">${infra.status}</span></p>
          <p><strong>Contact:</strong> ${infra.contactName} (${infra.contactNo})</p>
          <p><strong>Source:</strong> ${infra.source}</p>
          ${infra.isRented ? `<p><strong>Rented:</strong> ${infra.rentAmount}/month</p>` : ''}
        </div>
      </div>
    `;

    const infoWindow = new google.maps.InfoWindow({
      content: content
    });

    infoWindow.open(map, marker);
  };

  /**
   * Apply filters
   */
  const applyFilters = (infras: Infrastructure[]): Infrastructure[] => {
    let filtered = [...infras];

    if (filters.type) {
      filtered = filtered.filter(i => i.type === filters.type);
    }
    if (filters.source) {
      filtered = filtered.filter(i => i.source === filters.source);
    }
    if (filters.status) {
      filtered = filtered.filter(i => i.status === filters.status);
    }
    if (filters.state) {
      filtered = filtered.filter(i => i.address.state.toLowerCase().includes(filters.state!.toLowerCase()));
    }
    if (filters.city) {
      filtered = filtered.filter(i => i.address.city.toLowerCase().includes(filters.city!.toLowerCase()));
    }
    if (filters.searchTerm) {
      const term = filters.searchTerm.toLowerCase();
      filtered = filtered.filter(i =>
        i.name.toLowerCase().includes(term) ||
        i.uniqueId.toLowerCase().includes(term) ||
        i.networkId.toLowerCase().includes(term)
      );
    }

    return filtered;
  };

  /**
   * Handle KML file import
   */
  const handleKMLImport = async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    try {
      const text = await file.text();
      const parser = new DOMParser();
      const xmlDoc = parser.parseFromString(text, 'text/xml');

      const placemarks = xmlDoc.getElementsByTagName('Placemark');
      const newInfras: Infrastructure[] = [];

      for (let i = 0; i < placemarks.length; i++) {
        const placemark = placemarks[i];
        const name = placemark.getElementsByTagName('name')[0]?.textContent || `Import ${i + 1}`;
        const description = placemark.getElementsByTagName('description')[0]?.textContent || '';
        const coordinates = placemark.getElementsByTagName('coordinates')[0]?.textContent?.trim();

        if (coordinates) {
          const [lng, lat] = coordinates.split(',').map(parseFloat);

          // Determine type from name or description
          const type: InfrastructureType =
            name.toLowerCase().includes('subpop') || description.toLowerCase().includes('subpop')
              ? 'SubPOP'
              : 'POP';

          const infra: Infrastructure = {
            id: `kml_${Date.now()}_${i}`,
            type,
            name,
            uniqueId: `KML-${type}-${String(i + 1).padStart(3, '0')}`,
            networkId: `NET-${Date.now()}-${i}`,
            coordinates: { lat, lng },
            address: {
              street: '',
              city: '',
              state: '',
              pincode: ''
            },
            contactName: '',
            contactNo: '',
            isRented: false,
            structureType: 'Tower',
            upsAvailability: false,
            backupCapacity: '',
            powerSource: 'Grid',
            source: 'KML',
            kmlFileName: file.name,
            createdOn: new Date(),
            updatedOn: new Date(),
            status: 'Active',
            notes: description
          };

          newInfras.push(infra);
        }
      }

      if (newInfras.length > 0) {
        saveInfrastructures([...infrastructures, ...newInfras]);
        alert(`Successfully imported ${newInfras.length} infrastructure items from KML`);
      } else {
        alert('No valid placemarks found in KML file');
      }
    } catch (error) {
      console.error('Error importing KML:', error);
      alert('Failed to import KML file. Please check the file format.');
    }

    // Reset input
    event.target.value = '';
  };

  /**
   * Add new infrastructure
   */
  const handleAddInfrastructure = () => {
    if (!newInfraLocation || !formData.name || !formData.networkId) {
      alert('Please fill in all required fields and place a marker on the map');
      return;
    }

    const newInfra: Infrastructure = {
      id: `manual_${Date.now()}`,
      type: formData.type as InfrastructureType,
      name: formData.name,
      uniqueId: generateUniqueId(formData.type as InfrastructureType),
      networkId: formData.networkId,
      coordinates: newInfraLocation,
      address: formData.address || {
        street: '',
        city: '',
        state: '',
        pincode: ''
      },
      contactName: formData.contactName || '',
      contactNo: formData.contactNo || '',
      contactEmail: formData.contactEmail,
      isRented: formData.isRented || false,
      rentAmount: formData.rentAmount,
      agreementDates: formData.agreementDates,
      landlordName: formData.landlordName,
      landlordContact: formData.landlordContact,
      structureType: formData.structureType || 'Tower',
      height: formData.height,
      upsAvailability: formData.upsAvailability || false,
      upsCapacity: formData.upsCapacity,
      backupCapacity: formData.backupCapacity || '',
      powerSource: formData.powerSource || 'Grid',
      equipmentList: formData.equipmentList,
      connectedTo: formData.connectedTo,
      bandwidth: formData.bandwidth,
      source: 'Manual',
      createdOn: new Date(),
      updatedOn: new Date(),
      status: formData.status || 'Active',
      notes: formData.notes
    };

    saveInfrastructures([...infrastructures, newInfra]);
    alert('Infrastructure added successfully!');
    resetForm();
  };

  /**
   * Generate unique ID
   */
  const generateUniqueId = (type: InfrastructureType): string => {
    const count = infrastructures.filter(i => i.type === type && i.source === 'Manual').length + 1;
    return `${type}-MAN-${String(count).padStart(4, '0')}`;
  };

  /**
   * Reset form
   */
  const resetForm = () => {
    setFormData({
      type: 'POP',
      name: '',
      networkId: '',
      contactName: '',
      contactNo: '',
      isRented: false,
      structureType: 'Tower',
      upsAvailability: false,
      backupCapacity: '',
      powerSource: 'Grid',
      status: 'Active'
    });
    setNewInfraLocation(null);
    setShowAddForm(false);
    setIsPlacingMarker(false);
  };

  /**
   * Delete infrastructure
   */
  const handleDelete = (id: string) => {
    if (window.confirm('Are you sure you want to delete this infrastructure?')) {
      const updated = infrastructures.filter(i => i.id !== id);
      saveInfrastructures(updated);
    }
  };

  /**
   * Navigate to infrastructure on map
   */
  const navigateToInfra = (infra: Infrastructure) => {
    if (map) {
      map.setCenter(infra.coordinates);
      map.setZoom(15);

      const marker = markers.get(infra.id);
      if (marker) {
        showInfoWindow(marker, infra);
      }
    }
  };

  return (
    <div className="absolute top-20 left-4 bg-white dark:bg-gray-800 rounded-lg shadow-xl border border-gray-200 dark:border-gray-700 p-4 max-w-2xl z-10 max-h-[80vh] overflow-y-auto">
      {/* Header */}
      <div className="flex items-center justify-between mb-4 sticky top-0 bg-white dark:bg-gray-800 pb-2">
        <h3 className="text-lg font-semibold text-gray-900 dark:text-white flex items-center">
          <svg className="w-5 h-5 mr-2" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
          </svg>
          Infrastructure Management
        </h3>
        {onClose && (
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 dark:hover:text-gray-300"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        )}
      </div>

      {/* Stats */}
      <div className="grid grid-cols-4 gap-2 mb-4">
        <div className="p-3 bg-blue-50 dark:bg-blue-900/20 rounded-md text-center">
          <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
            {infrastructures.filter(i => i.type === 'POP').length}
          </div>
          <div className="text-xs text-gray-600 dark:text-gray-400">POPs</div>
        </div>
        <div className="p-3 bg-green-50 dark:bg-green-900/20 rounded-md text-center">
          <div className="text-2xl font-bold text-green-600 dark:text-green-400">
            {infrastructures.filter(i => i.type === 'SubPOP').length}
          </div>
          <div className="text-xs text-gray-600 dark:text-gray-400">SubPOPs</div>
        </div>
        <div className="p-3 bg-purple-50 dark:bg-purple-900/20 rounded-md text-center">
          <div className="text-2xl font-bold text-purple-600 dark:text-purple-400">
            {infrastructures.filter(i => i.source === 'KML').length}
          </div>
          <div className="text-xs text-gray-600 dark:text-gray-400">From KML</div>
        </div>
        <div className="p-3 bg-orange-50 dark:bg-orange-900/20 rounded-md text-center">
          <div className="text-2xl font-bold text-orange-600 dark:text-orange-400">
            {infrastructures.filter(i => i.source === 'Manual').length}
          </div>
          <div className="text-xs text-gray-600 dark:text-gray-400">Manual</div>
        </div>
      </div>

      {/* Action Buttons */}
      <div className="flex space-x-2 mb-4">
        <button
          onClick={() => setShowAddForm(!showAddForm)}
          className="flex-1 px-3 py-2 text-sm font-medium text-white bg-blue-600 rounded-md hover:bg-blue-700 flex items-center justify-center"
        >
          <svg className="w-4 h-4 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
          </svg>
          Add New
        </button>
        <label className="flex-1 px-3 py-2 text-sm font-medium text-gray-700 dark:text-gray-300 bg-green-100 dark:bg-green-900/20 rounded-md hover:bg-green-200 dark:hover:bg-green-900/30 cursor-pointer flex items-center justify-center">
          <svg className="w-4 h-4 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
          </svg>
          Import KML
          <input
            type="file"
            accept=".kml"
            onChange={handleKMLImport}
            className="hidden"
          />
        </label>
        <button
          onClick={() => setShowTable(!showTable)}
          className="flex-1 px-3 py-2 text-sm font-medium text-gray-700 dark:text-gray-300 bg-gray-100 dark:bg-gray-700 rounded-md hover:bg-gray-200 dark:hover:bg-gray-600 flex items-center justify-center"
        >
          <svg className="w-4 h-4 mr-1" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M3 10h18M3 14h18m-9-4v8m-7 0h14a2 2 0 002-2V8a2 2 0 00-2-2H5a2 2 0 00-2 2v8a2 2 0 002 2z" />
          </svg>
          {showTable ? 'Hide' : 'Show'} Table
        </button>
      </div>

      {/* Add Form */}
      {showAddForm && (
        <div className="mb-4 p-4 bg-gray-50 dark:bg-gray-900 rounded-md space-y-3">
          <h4 className="font-medium text-gray-900 dark:text-white mb-2">Add New Infrastructure</h4>

          {/* Type */}
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Type *</label>
            <select
              value={formData.type}
              onChange={(e) => setFormData({ ...formData, type: e.target.value as InfrastructureType })}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md dark:bg-gray-700 dark:text-white text-sm"
            >
              <option value="POP">POP</option>
              <option value="SubPOP">SubPOP</option>
            </select>
          </div>

          {/* Name */}
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Name *</label>
            <input
              type="text"
              value={formData.name}
              onChange={(e) => setFormData({ ...formData, name: e.target.value })}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md dark:bg-gray-700 dark:text-white text-sm"
              placeholder="e.g., Mumbai Central POP"
            />
          </div>

          {/* Network ID */}
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Network ID *</label>
            <input
              type="text"
              value={formData.networkId}
              onChange={(e) => setFormData({ ...formData, networkId: e.target.value })}
              className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md dark:bg-gray-700 dark:text-white text-sm"
              placeholder="e.g., NET-MUM-001"
            />
          </div>

          {/* Location */}
          <div>
            <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Location *</label>
            <button
              onClick={() => setIsPlacingMarker(true)}
              className={`w-full px-3 py-2 text-sm font-medium rounded-md ${
                newInfraLocation
                  ? 'bg-green-100 dark:bg-green-900/20 text-green-700 dark:text-green-400'
                  : 'bg-blue-100 dark:bg-blue-900/20 text-blue-700 dark:text-blue-400'
              }`}
            >
              {newInfraLocation
                ? ` Location Set: ${newInfraLocation.lat.toFixed(6)}, ${newInfraLocation.lng.toFixed(6)}`
                : isPlacingMarker
                ? 'Click on map to place marker...'
                : 'Click to Place Marker on Map'}
            </button>
          </div>

          {/* Contact */}
          <div className="grid grid-cols-2 gap-2">
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Contact Name</label>
              <input
                type="text"
                value={formData.contactName}
                onChange={(e) => setFormData({ ...formData, contactName: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md dark:bg-gray-700 dark:text-white text-sm"
              />
            </div>
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">Contact Number</label>
              <input
                type="text"
                value={formData.contactNo}
                onChange={(e) => setFormData({ ...formData, contactNo: e.target.value })}
                className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md dark:bg-gray-700 dark:text-white text-sm"
              />
            </div>
          </div>

          {/* Buttons */}
          <div className="flex space-x-2 pt-2">
            <button
              onClick={resetForm}
              className="flex-1 px-3 py-2 text-sm font-medium text-gray-700 dark:text-gray-300 bg-gray-100 dark:bg-gray-700 rounded-md hover:bg-gray-200 dark:hover:bg-gray-600"
            >
              Cancel
            </button>
            <button
              onClick={handleAddInfrastructure}
              className="flex-1 px-3 py-2 text-sm font-medium text-white bg-blue-600 rounded-md hover:bg-blue-700"
            >
              Add Infrastructure
            </button>
          </div>
        </div>
      )}

      {/* Table View */}
      {showTable && (
        <div className="mb-4">
          {/* Filters */}
          <div className="grid grid-cols-3 gap-2 mb-2">
            <select
              value={filters.type || ''}
              onChange={(e) => setFilters({ ...filters, type: e.target.value as InfrastructureType | undefined })}
              className="px-2 py-1 text-sm border border-gray-300 dark:border-gray-600 rounded dark:bg-gray-700 dark:text-white"
            >
              <option value="">All Types</option>
              <option value="POP">POP</option>
              <option value="SubPOP">SubPOP</option>
            </select>
            <select
              value={filters.source || ''}
              onChange={(e) => setFilters({ ...filters, source: e.target.value as any })}
              className="px-2 py-1 text-sm border border-gray-300 dark:border-gray-600 rounded dark:bg-gray-700 dark:text-white"
            >
              <option value="">All Sources</option>
              <option value="KML">KML</option>
              <option value="Manual">Manual</option>
            </select>
            <input
              type="text"
              value={filters.searchTerm || ''}
              onChange={(e) => setFilters({ ...filters, searchTerm: e.target.value })}
              placeholder="Search..."
              className="px-2 py-1 text-sm border border-gray-300 dark:border-gray-600 rounded dark:bg-gray-700 dark:text-white"
            />
          </div>

          {/* Table */}
          <div className="overflow-x-auto border border-gray-200 dark:border-gray-700 rounded-md max-h-96">
            <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700">
              <thead className="bg-gray-50 dark:bg-gray-900 sticky top-0">
                <tr>
                  <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 dark:text-gray-400">Name</th>
                  <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 dark:text-gray-400">Type</th>
                  <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 dark:text-gray-400">ID</th>
                  <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 dark:text-gray-400">Source</th>
                  <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 dark:text-gray-400">Status</th>
                  <th className="px-3 py-2 text-left text-xs font-medium text-gray-500 dark:text-gray-400">Actions</th>
                </tr>
              </thead>
              <tbody className="divide-y divide-gray-200 dark:divide-gray-700 bg-white dark:bg-gray-800">
                {applyFilters(infrastructures).map(infra => (
                  <tr key={infra.id} className="hover:bg-gray-50 dark:hover:bg-gray-700">
                    <td className="px-3 py-2 text-sm text-gray-900 dark:text-white">{infra.name}</td>
                    <td className="px-3 py-2 text-sm">
                      <span className={`px-2 py-1 rounded text-xs ${
                        infra.type === 'POP'
                          ? 'bg-blue-100 dark:bg-blue-900/20 text-blue-700 dark:text-blue-400'
                          : 'bg-green-100 dark:bg-green-900/20 text-green-700 dark:text-green-400'
                      }`}>
                        {infra.type}
                      </span>
                    </td>
                    <td className="px-3 py-2 text-sm font-mono text-gray-600 dark:text-gray-400">{infra.uniqueId}</td>
                    <td className="px-3 py-2 text-sm">
                      <span className={`px-2 py-1 rounded text-xs ${
                        infra.source === 'KML'
                          ? 'bg-purple-100 dark:bg-purple-900/20 text-purple-700 dark:text-purple-400'
                          : 'bg-orange-100 dark:bg-orange-900/20 text-orange-700 dark:text-orange-400'
                      }`}>
                        {infra.source}
                      </span>
                    </td>
                    <td className="px-3 py-2 text-sm">
                      <span className={`px-2 py-1 rounded text-xs ${
                        infra.status === 'Active' ? 'bg-green-100 text-green-700' :
                        infra.status === 'Inactive' ? 'bg-gray-100 text-gray-700' :
                        'bg-yellow-100 text-yellow-700'
                      }`}>
                        {infra.status}
                      </span>
                    </td>
                    <td className="px-3 py-2 text-sm space-x-1">
                      <button
                        onClick={() => navigateToInfra(infra)}
                        className="text-blue-600 hover:text-blue-800 dark:text-blue-400"
                        title="View on Map"
                      >
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                        </svg>
                      </button>
                      <button
                        onClick={() => handleDelete(infra.id)}
                        className="text-red-600 hover:text-red-800 dark:text-red-400"
                        title="Delete"
                      >
                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
                        </svg>
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Legend */}
      <div className="p-3 bg-gray-50 dark:bg-gray-900 rounded-md">
        <h4 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">Marker Legend</h4>
        <div className="grid grid-cols-2 gap-2 text-xs">
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 rounded-full bg-blue-500"></div>
            <span className="text-gray-600 dark:text-gray-400">Manual POP</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 rounded-full bg-cyan-400"></div>
            <span className="text-gray-600 dark:text-gray-400">Manual SubPOP</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-4 h-4 rounded-full bg-green-500"></div>
            <span className="text-gray-600 dark:text-gray-400">KML POP</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 rounded-full bg-lime-400"></div>
            <span className="text-gray-600 dark:text-gray-400">KML SubPOP</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default InfrastructureManagementTool;