import React, { useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import { Provider } from 'react-redux';
import { store } from './store';
import './App.css';

// Components
import NavigationBar from './components/common/NavigationBar';
import Dashboard from './pages/Dashboard';
import MapPage from './pages/MapPage';
import UsersPage from './pages/UsersPage';
import AdminPage from './pages/AdminPage';
import AnalyticsPage from './pages/AnalyticsPage';
import LoginPage from './pages/LoginPage';

// Context Providers
import { ThemeProvider } from './contexts/ThemeContext';
import { AuthProvider } from './contexts/AuthContext';
import { GoogleMapsProvider } from './contexts/GoogleMapsContext';

// Utils and Configuration
import { config, validateEnvironment, debugLog } from './utils/environment';

// Error Boundary
import { ErrorBoundary } from 'react-error-boundary';

// Error Fallback Component
const ErrorFallback: React.FC<{ error: Error; resetErrorBoundary: () => void }> = ({
  error,
  resetErrorBoundary
}) => (
  <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
    <div className="text-center">
      <div className="mx-auto h-12 w-12 flex items-center justify-center bg-red-100 rounded-lg mb-4">
        <svg className="h-6 w-6 text-red-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
          <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-2.5L13.732 4c-.77-.833-1.964-.833-2.732 0L3.732 16.5c-.77.833.192 2.5 1.732 2.5z" />
        </svg>
      </div>
      <h2 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
        Application Error
      </h2>
      <p className="text-sm text-gray-500 dark:text-gray-400 mb-4">
        {error.message}
      </p>
      <button
        onClick={resetErrorBoundary}
        className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
      >
        Try Again
      </button>
    </div>
  </div>
);

// Protected Route Component
const ProtectedRoute: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const isAuthenticated = store.getState().auth.isAuthenticated;

  if (!isAuthenticated) {
    return <Navigate to="/login" replace />;
  }

  return <>{children}</>;
};

const App: React.FC = () => {
  useEffect(() => {
    // Validate environment configuration on startup
    const validation = validateEnvironment();

    if (!validation.valid) {
      console.error('Environment validation failed:', validation.errors);
      validation.errors.forEach(error => {
        console.error(` ${error}`);
      });
    }

    // Log application startup information
    debugLog('Application starting...', {
      mode: config.app.mode,
      environment: config.app.environment,
      version: config.app.version,
      debug: config.app.debug,
      buildDate: config.app.buildDate,
    });

    // Set up global error handling
    const handleUnhandledRejection = (event: PromiseRejectionEvent) => {
      console.error('Unhandled promise rejection:', event.reason);
    };

    const handleError = (event: ErrorEvent) => {
      console.error('Global error:', event.error);
    };

    window.addEventListener('unhandledrejection', handleUnhandledRejection);
    window.addEventListener('error', handleError);

    return () => {
      window.removeEventListener('unhandledrejection', handleUnhandledRejection);
      window.removeEventListener('error', handleError);
    };
  }, []);

  return (
    <ErrorBoundary
      FallbackComponent={ErrorFallback}
      onError={(error, errorInfo) => {
        console.error('React Error Boundary caught an error:', error, errorInfo);
      }}
    >
      <Provider store={store}>
        <ThemeProvider>
          <GoogleMapsProvider>
            <AuthProvider>
              <Router>
                <div className="App min-h-screen bg-gray-50 dark:bg-gray-900">
                  <NavigationBar />
                  <main>
                    <Routes>
                      <Route path="/login" element={<LoginPage />} />
                      <Route
                        path="/dashboard"
                        element={
                          <ProtectedRoute>
                            <Dashboard />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/map"
                        element={
                          <ProtectedRoute>
                            <MapPage />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/users"
                        element={
                          <ProtectedRoute>
                            <UsersPage />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/admin"
                        element={
                          <ProtectedRoute>
                            <AdminPage />
                          </ProtectedRoute>
                        }
                      />
                      <Route
                        path="/analytics"
                        element={
                          <ProtectedRoute>
                            <AnalyticsPage />
                          </ProtectedRoute>
                        }
                      />
                      <Route path="/" element={<Navigate to="/dashboard" replace />} />
                      <Route
                        path="*"
                        element={
                          <div className="min-h-screen flex items-center justify-center bg-gray-50 dark:bg-gray-900">
                            <div className="text-center">
                              <h1 className="text-2xl font-bold text-gray-900 dark:text-white mb-4">
                                Page Not Found
                              </h1>
                              <p className="text-gray-600 dark:text-gray-400 mb-4">
                                The page you're looking for doesn't exist.
                              </p>
                              <Navigate to="/dashboard" replace />
                            </div>
                          </div>
                        }
                      />
                    </Routes>
                  </main>
                </div>
              </Router>
            </AuthProvider>
          </GoogleMapsProvider>
        </ThemeProvider>
      </Provider>
    </ErrorBoundary>
  );
};

export default App;